<template>
  <!-- FaqList.vue -->
  <div class="list row">
    <!-- Í≤ÄÏÉâÏñ¥ ÏãúÏûë -->
    <div class="col-md-8">
      <div class="input-group mb-3">
        <!-- üëÄtitle -> searchTitleÎ°ú Î∞îÍøà(Ï†ÑÏ≤¥Í≤ÄÏÉâ + ÌÉÄÏù¥ÌãÄÍ≤ÄÏÉâ Ìï©Ïπú Î≤ÑÏ†Ñ) üëÄ-->
        <input
          type="text"
          class="form-control"
          placeholder="Search by title"
          v-model="searchTitle" 
        />
        <!-- Í≤ÄÏÉâÏñ¥ Î≤ÑÌäº -->
        <div class="input-group-append">
          <button
            class="btn btn-outline-secondary"
            type="button"
            @click="
              page = 1;
              retrieveFaq();
            "
          >
            Search
          </button>
        </div>
      </div>
    </div>
    <!-- Í≤ÄÏÉâÏñ¥ ÎÅù -->


    <!-- Ï∂îÍ∞Ä -->
    <!-- <!‚Äî ÌéòÏù¥Ïßï + Ï†ÑÏ≤¥ Î™©Î°ù ÏãúÏûë ‚Äî> -->
    <!-- <!‚Äî ÌéòÏù¥Ïßï ÏñëÏãù ÏãúÏûë ‚Äî> -->
    <div class="col-md-12">
      <!-- <!‚Äî ÏÖÄÎ†âÌä∏ Î∞ïÏä§ : Í∏∞Î≥∏ ÌéòÏù¥ÏßÄ Î≥ÄÍ≤Ω ‚Äî> -->
      <!-- <!‚Äî change :  üí´handlePageSizeChange($event), 1ÌéòÏù¥ÏßÄÎãπ Í∞úÏàò Î≥ÄÍ≤Ω Ïãú Ïã§ÌñâÎêòÎäî Ïù¥Î≤§Ìä∏‚Äî>üí´ -->
      <!-- <!‚Äî           $event : html ÌÉúÍ∑∏Ïùò Í∏∞Î≥∏ Ïù¥Î≤§Ìä∏, Ïù¥ Í∞ùÏ≤¥Î°ú ÌòÑÏû¨ ÏÑ†ÌÉù ÎòêÎäî ÌÅ¥Î¶≠Ìïú htmlÌÉúÍ∑∏(ÏñëÏãù)Î•º Ïïå Ïàò ÏûàÏùå.  -->
                     <!-- event.target (ÌòÑÏû¨ ÏÇ¨Ïö©ÏûêÍ∞Ä ÏÑ†ÌÉùÌïú ÏñëÏãù(Ïó¨Í∏∞ÏÑúÎäî ÏÖÄÎ†âÌä∏ Î∞ïÏä§ÎÇ¥ Î™©Î°ùÏùÑ ÏùòÎØ∏ ) ‚Äî> -->
      <div class="mb-3">
        Items per Page:
        <select v-model="pageSize" @change="handlePageSizeChange($event)">
          <option v-for="size in pageSizes" :key="size" :value="size">
            {{ size }}
          </option>
        </select>
      </div>

      <!-- <!‚Äî b-pagination : üëÄbootstrap-vueÏùò ÌéòÏù¥ÏßÄ Î≤àÌò∏ Ïª®Ìä∏Î°§ _Ïô∏Î∂ÄÎùºÏù¥Î∏åÎü¨Î¶¨ ÏÑ§ÏπòÌï¥Ïïº b-pa.. ÌÉúÍ∑∏ ÏÇ¨Ïö©Í∞ÄÎä• ‚Äî> -->
      <!-- Îß§Í∞úÎ≥ÄÏàò page, count Îßå ÎÑòÍ∏∞Î©¥ ÏûêÎèôÏúºÎ°ú ÎèôÏûë! Îß§Ïö∞ Ìé∏Î¶¨ (Ï¥ù ÌéòÏù¥ÏßÄ, ÌéòÏù¥ÏßÄÎãπ ÌëúÏãúÌï† Îç∞Ïù¥ÌÑ∞ Í±¥Ïàò) -->
      <!-- <!‚Äî total-rows : Ï†ÑÏ≤¥ Îç∞Ïù¥ÌÑ∞ Í∞úÏàò ‚Äî> -->
      <!-- <!‚Äî per-page : 1ÌéòÏù¥ÏßÄ Îãπ Í∞úÏàò ‚Äî> -->
      <!-- prev-text="ÌôîÎ©¥Ïóê Î≥¥Ïùº Í∏ÄÏûê"  : Ï†Ñ ÌéòÏù¥ÏßÄ Ïù¥Îèô-->
      <!-- next-text="ÌôîÎ©¥Ïóê Î≥¥Ïùº Í∏ÄÏûê"  : Îã§Ïùå ÌéòÏù¥ÏßÄ Ïù¥Îèô-->
      <!-- <!‚Äî change : üî±handlePageChange(), ÌéòÏù¥ÏßÄ Î≤àÌò∏ Î≤ÑÌäº ÌÅ¥Î¶≠ Ïãú Ïã§ÌñâÎêòÎäî Ïù¥Î≤§Ìä∏(Ïû¨Ï°∞Ìöå Ìï®Ïàò) ‚Äî> -->
      <b-pagination
        v-model="page"
        :total-rows="count"
        :per-page="pageSize"
        prev-text="Prev"
        next-text="Next"
        @change="handlePageChange"
      ></b-pagination>
    </div>
    <!-- <!‚Äî ÌéòÏù¥Ïßï ÏñëÏãù ÎÅù ‚Äî> -->


    <!-- Ï†ÑÏ≤¥ Î™©Î°ù Ï°∞Ìöå ÏãúÏûë -->
    <div class="col-md-6">
      <h4>Faq List</h4>
      <ul class="list-group">
        <li
          class="list-group-item"
          :class="{ active: index == currentIndex }"
          v-for="(data, index) in faq"
          :key="index"
          @click="setActiveFaq(data, index)"
