          {{ data.title }}
        </li>
      </ul>
      <!-- Ï†ÑÏ≤¥ Î™©Î°ù Ï°∞Ìöå ÎÅù -->

      <!-- ÏÇ≠Ï†ú Î≤ÑÌäº ÏãúÏûë -->
      <button class="m-3 btn btn-sm btn-danger" @click="removeAllFaq">
        Remove All
      </button>
      <!-- ÏÇ≠Ï†úÎ≤ÑÌäº ÎÅù -->
    </div>
    <!-- <!‚Äî ÌéòÏù¥Ïßï + Ï†ÑÏ≤¥ Î™©Î°ù ÎÅù ‚Äî> -->


    <!-- Í∞ÑÎûµ ÏÉÅÏÑ∏ Î™©Î°ù ÏãúÏûë -->
    <div class="col-md-6">
      <div v-if="currentFaq">
        <h4>Faq</h4>
        <div>
          <label><strong>Title:</strong></label> {{ currentFaq.title }}
        </div>
        <div>
          <label><strong>Loc:</strong></label> {{ currentFaq.content }}
        </div>
        <!-- Í≥†Í∞ùÏ†ïÎ≥¥ ÏàòÏ†ï Î≤ÑÌäº -->
        <router-link :to="'/faq/' + currentFaq.no"
          ><span class="badge bg-success">Edit</span></router-link
        >
      </div>
      <div v-else>
        <br />
        <p>Please click on a Faq...</p>
      </div>
    </div>
    <!-- Í∞ÑÎûµ ÏÉÅÏÑ∏ Î™©Î°ù ÎÅù -->
  </div>
</template>

<script>
// axios Í≥µÌÜµÌï®Ïàò import
import FaqDataService from "../../services/FaqDataService";

export default {
  // Î≥ÄÏàò Ï†ïÏùòÌïòÎäî Í≥≥ : data(), Ï¥àÍ∏∞Ìôî
  data() {
    return {
      faq: [],
      currentFaq: null,
      currentIndex: -1,
      // title: "", ÏÜåÏä§ Î≥ÄÍ≤Ω-> searchTitle: "",
      searchTitle: "",  // Í≤ÄÏÉâÏñ¥. title -> searchTitleÎ°ú Î≥ÄÍ≤Ω -> Í∏∞Ï°¥Ïùò searchTitle Ìï®Ïàò(likeÍ≤ÄÏÉâ) ÏïàÏì∞ÎãàÍπå ÎßâÏïÑÏïº Ìï®

      // ÌéòÏù¥ÏßïÏùÑ ÏúÑÌïú Î≥ÄÏàò Ï†ïÏùò(ÏµúÏ¥àÏÑ∏ÌåÖ) 4Í∞ÄÏßÄ
      page: 1,      // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ
      count: 0,     // Ï†ÑÏ≤¥ Îç∞Ïù¥ÌÑ∞ Í±¥Ïàò
      pageSize: 3,  // Ìïú ÌéòÏù¥ÏßÄÎãπ ÌôîÎ©¥Ïóê Î≥¥Ïó¨Ï§Ñ Í∞úÏàòÎ•º Í≤∞Ï†ïÌïòÎäî Î≥ÄÏàò

      pageSizes: [3, 6, 9]  // ÏÖÄÎ†âÌä∏Î∞ïÏä§Ïóê ÎÑ£ÏùÑ Í∏∞Î≥∏ Îç∞Ïù¥ÌÑ∞. ÏÑ†ÌÉùÏ∞Ω
    };
  },
  // Ìï®Ïàò Ï†ïÏùòÌïòÎäî Í≥≥ : methods:
  methods: {
    // axios , Î™®Îì† Î∂ÄÏÑú Ï†ïÎ≥¥ Ï°∞Ìöå ÏöîÏ≤≠ Ìï®Ïàò
    retrieveFaq() {
      // getAll(dname, page, size)Î°ú Ìï®Ïàò Î≥ÄÍ≤ΩÎê®
      FaqDataService.getAll(this.searchTitle, this.page-1, this.pageSize)  // ÏûêÎèô ÌéòÏù¥ÏßïÏ≤òÎ¶¨Í∞Ä 1Î∂ÄÌÑ∞ ÏãúÏûëÌïòÎØÄÎ°ú -1Ìï¥ÏÑú 0ÏúºÎ°ú ÎßåÎì§Ïñ¥ Ï§å
        // axiosÏÑ±Í≥µÌïòÎ©¥ Ïä§ÌîÑÎßÅÎ∂ÄÌä∏ Í≤∞Í≥ºÍ∞Ä .then() ÏïàÏúºÎ°ú Ï†ÑÏÜ°Îê®
        .then((response) => {
          // this.dept = response.data;  -> Î≥ÄÍ≤Ω -> const {dept, totalItems} = response.data;
          // const(let){ÏÜçÏÑ±Î™Ö, ÏÜçÏÑ±Î™Ö2} = Îç∞Ïù¥ÌÑ∞Í∞ùÏ≤¥Î∞∞Ïó¥  : Î™®ÎçòÏûêÎ∞îÏä§ÌÅ¨Î¶ΩÌä∏ Î¨∏Î≤ï Íµ¨Ï°∞Î∂ÑÌï¥Ìï†Îãπ
          const {faq, totalItems} = response.data; // springboot ÏóêÏÑú Ï†ÑÏÜ°Îêú Îßµ(map) Ï†ïÎ≥¥
          this.faq = faq;         // Ïä§ÌîÑÎßÅ Î∂ÄÌä∏ÏóêÏÑú Ï†ÑÏÜ°Ìïú Îç∞Ïù¥ÌÑ∞
          this.count = totalItems;  // Ïä§ÌîÑÎßÅÎ∂ÄÌä∏ÏóêÏÑú Ï†ÑÏÜ°Ìïú ÌéòÏù¥ÏßÄÏ†ïÎ≥¥(Ï¥ù Í±¥Ïàò)
          // ÎîîÎ≤ÑÍπÖ ÏΩòÏÜîÏóê Ï†ïÎ≥¥ Ï∂úÎ†•
          console.log(response.data);
        })
        // Ïã§Ìå®ÌïòÎ©¥ .catch() Ïóê ÏóêÎü¨Í∞Ä Ï†ÑÏÜ°Îê®
        .catch((e) => {
          console.log(e);
        });
    },
    // Î™©Î°ùÏùÑ ÌÅ¥Î¶≠ÌñàÏùÑÎïå ÌòÑÏû¨ Î∂ÄÏÑúÍ∞ùÏ≤¥, Ïù∏Îç±Ïä§Î≤àÌò∏Î•º Ï†ÄÏû•ÌïòÎäî Ìï®Ïàò
    setActiveFaq(data, index) {
      this.currentFaq = data;
      this.currentIndex = index;
    },
    // axios, Î™®Îì† Î∂ÄÏÑú Ï†ïÎ≥¥ ÏÇ≠Ï†ú ÏöîÏ≤≠ Ìï®Ïàò
    removeAllFaq() {
      FaqDataService.deleteAll()
        // ÏÑ±Í≥µÌïòÎ©¥ .then() Í≤∞Í≥ºÍ∞Ä Ï†ÑÏÜ°Îê®
        .then((response) => {
          // ÎîîÎ≤ÑÍπÖ ÏΩòÏÜîÏóê Ï†ïÎ≥¥ Ï∂úÎ†•
          console.log(response.data);
          // Ï†ÑÏ≤¥ Î™©Î°ù Ïû¨Ï°∞Ìöå
          this.retrieveFaq();
          // currentFaq, currentIndex Ï¥àÍ∏∞Ìôî
          this.currentFaq = null;
          this.currentIndex = -1;
        })
        // Ïã§Ìå®ÌïòÎ©¥ .catch() Ïóê ÏóêÎü¨Í∞Ä Ï†ÑÏÜ°Îê®
        .catch((e) => {
          console.log(e);
        });
    },
    // // like Í≤ÄÏÉâ Ìï®Ïàò : Î∂ÄÏÑúÎ™ÖÏúºÎ°ú Ï°∞ÌöåÌïòÎäî Ìï®Ïàò
    // searchTitle() {
    //   FaqDataService.findByTitle(this.title)
    //     // ÏÑ±Í≥µÌïòÎ©¥ .then() Í≤∞Í≥ºÍ∞Ä Ï†ÑÏÜ°Îê®
    //     .then((response) => {
    //       // ÎîîÎ≤ÑÍπÖ ÏΩòÏÜîÏóê Ï†ïÎ≥¥ Ï∂úÎ†•
    //       this.faq = response.data; // like Í≤ÄÏÉâ Í≤∞Í≥ºÎ•º Î≥ÄÏàòÏóê Ï†ÄÏû•
    //       console.log(response.data);
    //     })
    //     // Ïã§Ìå®ÌïòÎ©¥ .catch() Ïóê ÏóêÎü¨Í∞Ä Ï†ÑÏÜ°Îê®
    //     .catch((e) => {
    //       console.log(e);
    //     });
    // },

    // select box Í∞í Î≥ÄÍ≤ΩÏãú Ïã§ÌñâÎêòÎäî Ìï®Ïàò(Ïû¨Ï°∞Ìöå)üí´
    // event : ÏÇ¨Ïö©ÏûêÍ∞Ä ÌÅ¥Î¶≠Ìïú Ïª®Ìä∏Î°§Ïù¥ Ïù¥Î≤§Ìä∏Í∞ùÏ≤¥Î•º ÌÜµÌï¥ Ï†ëÍ∑º,ÌôïÏù∏Í∞ÄÎä•
    handlePageSizeChange(event){
      this.pageSize = event.target.value    // ÏÖÄÎ†âÌä∏Î∞ïÏä§ Í∞í Í∞ÄÏ†∏Ïò§Í∏∞ -> Ìïú ÌéòÏù¥ÏßÄÎãπ Ï∂úÎ†•Ìï† Îç∞Ïù¥ÌÑ∞ Í∞ØÏàò Ï†ÄÏû•
      this.page = 1;    // ÌòÑÏû¨ÌéòÏù¥ÏßÄÎ•º Ï≤´ ÌéòÏù¥ÏßÄÎ°ú ÏÑ∏ÌåÖ
      // Ïû¨Ï°∞ÌöåÌï®Ïàò Ìò∏Ï∂ú
      this.retrieveFaq();
    },
    // ÌéòÏù¥ÏßÄÎ≤àÌò∏ Î≥ÄÍ≤Ω Ïãú Ïã§ÌñâÎêòÎäî Ìï®Ïàò(Ïû¨Ï°∞Ìöå)üî±
    handlePageChange(value){
      // Îß§Í∞úÎ≥ÄÏàòÍ∞íÏúºÎ°ú ÌòÑÏû¨ ÌéòÏù¥ÏßÄÎ≤àÌò∏ Î≥ÄÍ≤Ω
      this.page = value;
      // Ïû¨Ï°∞Ìöå Ìï®Ïàò Ìò∏Ï∂ú
      this.retrieveFaq();
    },

  },
  // ÌôîÎ©¥Ïù¥ Îú®ÏûêÎßàÏûê Ïã§ÌñâÎêòÎäî Ïù¥Î≤§Ìä∏(ÎùºÏù¥ÌîÑ ÏÇ¨Ïù¥ÌÅ¥ Ìï®Ïàò) : mounted(), created()
  mounted() {
    this.retrieveFaq(); // üëÄÌôîÎ©¥ Î°úÎî©Ïãú Ï†ÑÏ≤¥ Ï°∞ÌöåÌï®Ïàò retrieveFaq() Ìï®Ïàò Ïã§Ìñâ
  },
};
</script>

<style>
